
figure {
  margin: 1em 40px; 
}

@charset "UTF-8";
@font-face {
  font-family: 'Chancery';
  src: url("../fonts/chancery/apple-chancery-webfont.eot");
  src: local("Apple Chancery"), url("../fonts/chancery/apple-chancery-webfont.eot?#iefix") format("embedded-opentype"), url("../fonts/chancery/apple-chancery-webfont.woff2") format("woff2"), url("../fonts/chancery/apple-chancery-webfont.woff") format("woff"), url("../fonts/chancery/apple-chancery-webfont.ttf") format("truetype"), url("../fonts/chancery/apple-chancery-webfont.svg#apple-chancery") format("svg");
  font-weight: lighter;
  font-style: normal; }

#codediv {
  float: left;
  width: 440px;
  margin-top: 130.5px;
}

code, pre {
  padding: 7px;
  font-size: 13px;
  font-family: Consolas, Monaco, Menlo, Consolas, monospace;
}


.highlight {
margin: 1em 0;
border-radius: 5px;
overflow-x: auto;
box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.125); }
.highlight table {
  position: relative; }
  .highlight table::after {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    padding: 2px 7px;
    font-size: 13px;
    font-weight: bold;
    color: #cacaca;
    background: #f4efe1;
    content: 'Code'; }
.highlight.html > ::after {
  content: "HTML"; }
.highlight.js > ::after {
  content: "JavaScript"; }
.highlight.bash > ::after {
  content: "Bash"; }
.highlight.css > ::after {
  content: "CSS"; }
.highlight.scss > ::after {
  content: "Scss"; }
.highlight.java > ::after {
  content: "Java"; }
.highlight.xml > ::after {
  content: "XML"; }
.highlight.python > ::after {
  content: "Python"; }
.highlight.json > ::after {
  content: "JSON"; }
.highlight.swift > ::after {
  content: "Swift"; }
.highlight.ruby > ::after {
  content: "Ruby"; }
.highlight.php > ::after {
  content: "PHP"; }
.highlight.c > ::after {
  content: "C"; }
.highlight.cpp > ::after {
  content: "C++"; }
.highlight.scheme > ::after {
  content: "Scheme"; }
.highlight.objectivec > ::after {
  content: "Objective-C"; }
.highlight.yml > ::after {
  content: "YAML"; }
.highlight.stylus > ::after {
  content: "Stylus"; }
.highlight .code pre {
  margin: 0;
  padding: 30px 10px 10px; }
.highlight .gutter {
  width: 10px;
  color: #cacaca; }
  .highlight .gutter pre {
    margin: 0;
    padding: 30px 7px 10px; }
.highlight .line {
  height: 20px; }
.highlight table, .highlight tr, .highlight td {
    background:#f8f5ec;
  margin: 0;
  padding: 0;
  width: 100%;
  border-collapse: collapse; }
.highlight .code .comment,
.highlight .code .quote {
  color: #93a1a1; }
.highlight .code .keyword,
.highlight .code .selector-tag,
.highlight .code .addition {
  color: #859900; }
.highlight .code .number,
.highlight .code .string,
.highlight .code .meta .meta-string,
.highlight .code .literal,
.highlight .code .doctag,
.highlight .code .regexp {
  color: #2aa198; }
.highlight .code .attribute,
.highlight .code .attr,
.highlight .code .variable,
.highlight .code .template-variable,
.highlight .code .class .title,
.highlight .code .type {
  color: #b58900; }
.highlight .code .symbol,
.highlight .code .bullet,
.highlight .code .subst,
.highlight .code .meta,
.highlight .code .meta .keyword,
.highlight .code .selector-attr,
.highlight .code .selector-pseudo,
.highlight .code .link {
  color: #cb4b16; }
.highlight .code .built_in,
.highlight .code .deletion {
  color: #dc322f; }
